.header {
   
    // max-width: 675px;
    max-width: 751px;
    width: 100%;
}

.header-hello {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 37px;
}

.header-description {
    margin-top: 24px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 150%;
}

.header-more {
    margin-top: 24px;
    margin-top: 24px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 150%;
}

@media (max-width: 600px) {
    .header-hello {
        font-size: 26px;
        line-height: 32px;
    }

    .header-description,
    .header-more {
        font-size: 18px;
    }
}